
i.Nav{
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.0975);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 2;
  -webkit-transition: height 0.2s ease-in-out;
  transition: height 0.2s ease-in-out;
  height: 77px; 
}
.Nav-menus{
   display: flex;
   flex-direction: row;
   height: 77px;
   width: 70%;
   margin: 0 auto;
   padding: 26px 40px;
}
.Nav-brand-logo{
   display: block;
   background-position:-176px 0px;
   background-image: url(../src/components/foto/sprite.png);
   background-size: 405px 379px;
   background-repeat: no-repeat;
   height: 35px;
   width: 176px;
   text-indent: -1000%;
}
.Post{
   border-radius: 3px;
   border: 1px solid #e6e6e6;
   background-color: #fff;
   margin-bottom: 60px;
   margin-left: 20%;
   margin-right: 20%;
}
.Post-user{
   display: flex;
   padding: 16px;
   align-items: center;
}
.Post-user-avatar{
   width: 30px;
   height: 30px;
}
.Post-user-avatar img{
   width: 100%;
   height: 100%;
   border-radius: 50%;
}
.Post-user-nickname{
   margin-left: 12px;
   font-family: 'PT Sans', sans-serif;
   font-weight: bold;
}
.Post-image img{
   display: block;
   width: 100%;
}
.Post-caption{
   padding: 16px 16px;
}
.Post-caption strong{
   font-family: 'PT Sans', sans-serif;
   font-weight: bold;
}
.vjs-fade-out{
   display: none;
   visibility: hidden;
   opacity: 0;
}